export default {
	data() {
		return {
			modalName: '',
			checkbox: [{
				value: 0,
				name: '10元',
				checked: false,
				hot: false,
			}, {
				value: 1,
				name: '20元',
				checked: true,
				hot: false,
			}, {
				value: 2,
				name: '30元',
				checked: true,
				hot: true,
			}, {
				value: 3,
				name: '60元',
				checked: false,
				hot: true,
			}, {
				value: 4,
				name: '80元',
				checked: false,
				hot: false,
			}, {
				value: 5,
				name: '100元',
				checked: false,
				hot: false,
			}],
			CustomBar: this.CustomBar
		}
	},
	computed: {
		drawerStyle() {
			var Custom = this.CustomBar
			var style = `top: ${Custom}px;height:calc${(1350 - Custom)}px`
			return style
		}
	},
	methods: {
		showModal(e) {
			let that = this
			that.modalName = e.currentTarget.dataset.target
		},
		hideModal() {
			let that = this
			that.modalName = null
		},
		chooseCheckbox(e) {
			let that = this
			let items = that.checkbox
			let values = e.currentTarget.dataset.value
			for (let i = 0; i < items.length; i ++) {
				if (items[i].value == values) {
					items[i].checked = !items[i].checked;
					break
				}
			}
			that.checkbox = items
		}
	}
}
